<template>
	<div id="newsList">
        <ul class="news_list">
            <li v-for="(item, index) in data" v-if="index < num" :key="index" class="news_li_item" @click="toDetail(item)">
                <div class="news_li_cont full_title">
                    <h2 class="news_li_title_all">({{ index + 1 }}) {{ item.title }}</h2>
                    <div  class="news_li_tag_time">
                        <span v-for="(tag, i) in item.keyWords" v-if="i < 4" :key="i" class="tag fl">{{ tag }}</span>
                        <span class="news_li_time fr">{{ item.publishDate }}</span>	
                    </div>	
                </div>
            </li>
        </ul>
	</div>
</template>

<script>
    // 通用JS
    import Common from 'common/common.js';
    import { SetCookie } from 'common/important.js';
    // Api方法
	import Api from "api/message.js";
    
	export default {
        name: "newsList",
        /* 
        * 获取父级传值
        * 资讯列表 data
        * 显示数量 num
        * 页面类型 pageType: 1 父级页面是资讯列表页, 2 父级页面是消息通知页
        * 是否保存页面滚动高度 saveScrollH
        */
        props: {
			data:{
				type: Array,
				default: () => {
                    return [];
                }
            },
            num:{
                type: Number,
                default: 5
            },
            pageType:{
                type: Number,
                default: null
            },
            saveScrollH: {
                type: Boolean,
                default: true
            }
		},
		data(){
			return{}
        },
        methods:{
            // 跳转到详情页
			toDetail(item){
                // 跳转详情页
                // if(this.saveScrollH){
                //     // 列表滚动高度存缓存
                //     SetCookie('scrollH', $(window).scrollTop());
                // }
                
                // if(this.pageType){
                //     Common.GotoPage('NewsDetail', { newsId: item.id, type: this.pageType }, this);
                // }
                // else Common.GotoPage('NewsDetail', { newsId: item.id }, this);

                // 新窗口打开
                window.open(item.url);
                
                // 标记已读
                Api.Readed([item.id])
				.then(res => {
					if(res.code == 200) return true;
					else console.log(res.msg);
				})
				.catch(err => console.log(err))
			},
        }
	};
</script>

<style lang="less">
	// 引入通用设置文件
    @import "../../assets/less/setting";
    @import "../../assets/less/news_list";
</style>



